import React from 'react';

export default () => (
  <div className="margin--top-10">
    <h1 className="margin--bottom-10">The Archadon Guarantee</h1>
    <h2 className="margin--bottom-3">If youre not happy, neither are we. We offer:</h2>
    <p> Heirloom-quality, handcrafted rugs</p>
    <p> Designs for every style and taste</p>
    <p> Free US shipping</p>
    <p> Easy returns and exchanges (within 30 days on rugs in like-new condition)</p>
    <p> Free return shipping</p>
  </div>
);



